let $ajax = {};

let ajax = function(setting) {
  let promise = new Promise();

  let method = setting.method || "get";
  let callback = setting.success || function() {};
  let params = setting.params || "";
  let dataType = setting.dataType || "";
  let beforeSend = setting.beforeSend || undefined;
  let asnyc = setting.asnyc || true;
  let error = setting.error || function() {};
  let url = setting.url || function() {};

  var xhr = new XMLHttpRequest();

  xhr.onreadystatechange = function() {
    if (xhr.readyState == 4) {
      // ready
      var statusCode = xhr.status;
      if (statusCode == 200) {
        promise.resolve(JSON.parse(xhr.responseText));
      }else{
      	promise.reject(xhr);
      }
    } else {
      // not ready
    }
  };
  xhr.open(method, url, asnyc);
  xhr.send(params);
  return promise;
};

$ajax.get = ajax;

$ajax.get({
  url: 'http://zhanglun.daoapp.io/api/tasks'
}).then(function(res){
	console.log(res);
});

module.exports = $ajax;
